.login-container {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}

.left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.left-content {
  width: 60%;
}

.left-form {
  margin-top: 40px;
}

.left .left-content img {
  width: 200px;
}

.left .left-content h3 {
  font-family: 'Inter';
  font-weight: 700;
}

.right {
  display: grid;
  place-items: center;
  background: #3f7fca;
}

.right img {
  width: 70%;
}

@media (max-width: 900px) {
  .login-container {
      grid-template-columns: 1fr;
      gap: 50px;
    }

  .left-content {
    width: 90%;
  }

  .right {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
}